@import "../mixins";
.fg {
  position: relative;
  margin: 0;
  label{
    font-weight: 500;
    font-size: .875em;
    line-height: 143%;
    color: #344054;
    display: block;
    &~textarea,
    &+input,
    &~.nice-select{
      margin-top: .22em;
    }
  }
  .nice-select{
    float: unset;
    width: 100%;
    height: unset;
    background-color: transparent !important;
    border: none !important;
    padding: 0;
    font-size: 1em;
    .current{
      display: flex;
      align-items: center;
      background-image: url(../img/arrow-down.svg);
      background-repeat: no-repeat;
      background-position: right 1em center;
      background-size: .75em auto;
    }
    &.open{
      .current{
        background-image: url(../img/arrow-up.svg);
        border-color: $accent;
      }
    }
  }
  .list{
    min-width: 100%;
    width: auto;
    margin-top: .25em;
    background: #FFFFFF;
    box-shadow: 0px 12px 16px -4px rgba(16, 24, 40, 0.1), 0px 4px 6px -2px rgba(16, 24, 40, 0.05);
    border-radius: .5em;
    border: none;
    padding: .25em 0;
  }
  .option{
    height: 2.75em;
    display: flex;
    align-items: center;
    font-size: 1em;
    padding: 0 4em 0 .875em;
    font-weight: 400;
    border: none !important;
    background-repeat: no-repeat;
    background-position: right .875em center;
    background-size: 1.25em auto;
    &.focus,
    &.selected,
    &:hover{
      background-color: #FCFAFF !important;
      font-weight: 400 !important;
    }
    &.selected{
      background-image: url(../img/check.svg);
    }
    &.disabled{
      display: none;
    }
  }
  .current,
  input,
  textarea{
    width: 100%;
    height: 2.78em;
    font-size: 1.125em;
    line-height: 150%;
    padding: 0 .68em;
    border: $border;
    border-radius: .44em;
    color: $black;
    &::placeholder {
      color: $lightGrey;
    }
    &:focus,
    &:active{
      border-color: $accent;
    }
    &:disabled{
      background: #F9FAFB;
      pointer-events: none;
    }
  }
  textarea{
    height: 10.875em;
    resize: none;
    padding-top: .625em;
    padding-bottom: .625em;
  }
  &__icon{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: .875em;
    &~input{
      padding-left: 2.625em;
    }
  }
  &__hint,
  &__error-text{
    margin-top: .375em;
  }
  &__error-text{
    display: none;
  }
  .pass-toggle{
    position: absolute;
    right: .875em;
    bottom: .75em;
    width: 1.25em;
    height: 1.25em;
    background-image: url(../img/eye-off.svg);
    @include bgImgSize(100%);
    font-size: 1em;
    &._active{
      background-image: url(../img/eye.svg);
    }
    &~input{
      padding-right: 2.625em;
    }
  }
  &._error{
    label{
      color: $red;
    }
    input{
      border-color: #FDA29B;
      box-shadow: 0px 1px 2px rgba(240, 68, 56, 0.05), 0px 0px 0px 4px rgba(240, 68, 56, 0.1);
      background-image: url(../img/alert.svg);
      background-size: 1em auto;
      background-position: right .875em center;
      background-repeat: no-repeat;
    }
    .fg__error-text{
      display: block;
    }
  }
}

.fg-linear{
  height: 3em;
  display: flex;
  align-items: center;
  justify-content: center;
  &__border{
    position: absolute;
    height: 100%;
    width: 100%;
    background-image: $linear2;
    left: 0;
    top: 0;
    border-radius: .5em;
  }
  input{
    position: relative;
    z-index: 2;
    height: calc(100% - 4px);
    width: calc(100% - 4px);
    &:focus,
    &:active,
    &:hover{
      border-color: transparent;
      &~.fg-linear__border{
        background-image: $linear;
      }
    }
  }
}


.check {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {
        // background-color: $green;
        border: 3px solid #fff;
      }
    }
    &:disabled+label {
      color: #AAAAAA;
      pointer-events: none;
    }
  }
  label {
    padding-left: 40px;
    position: relative;
    min-height: 20px;
    font-size: 14px;
    line-height: 20px;
    cursor: pointer;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      border-radius: 2px;
      background: rgba(255, 255, 255, 0.01);
      border: 1px solid rgba(255, 255, 255, 0.45);
      width: 20px;
      height: 20px;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      transition: all .100s;
    }
  }
}
