@import "vars";
@mixin font-face($name, $file, $weight: 400, $style: normal) {
    @font-face {
        font-family: "#{$name}";
        src: url('../fonts/#{$file}.ttf') format('ttf'),
        url('../fonts/#{$file}.woff2') format('woff2'),
        url('../fonts/#{$file}.woff') format('woff'),
        url('../fonts/#{$file}.eot') format('eot');
        font-weight: $weight;
        font-style: $style;
        font-display: swap;
    }
}

@mixin scroll {
    &::-webkit-scrollbar {
        width: 3px;
    }
    &::-webkit-scrollbar-track {
        background: $yellow;
        border-radius: 3px;
    }
    &::-webkit-scrollbar-thumb {
        background: $black;
        border-radius: 3px;
    }
}

@mixin imgCover {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

@mixin bgImgSize($size) {
    background-size: $size auto;
    background-repeat: no-repeat;
    background-position: center;
}

@mixin center {
    display: flex;
    align-items: center;
    justify-content: center;
}

@mixin absolute {
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}

@mixin grid100gap($gap) {
    display: grid;
    grid-template-columns: 100%;
    gap: $gap;
}

@mixin grid3 {
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
    gap: 1.75em 1.25em;
    @media screen and (max-width: 992px){
        grid-template-columns: 100%;
    }
}
