@import "../mixins";
.banner{
  img{
    width: 100%;
    display: block;
  }
}

.banner2{
  display: none;
}

.header{
  position: relative;
  &__logo{
    display: block;
    width: 16.25em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__buttons{
    display: none;
  }
}

.header-top{
  padding: 1.25em 0;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.header-search{
  width: 30.1875em;
  position: relative;
  margin-right: 10.125em;
  .fg{
    input{
      background-color: #fff;
      border: 2px solid #fff;
    }
  }
  &__btn{
    font-size: 1em;
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    width: 3em;
    background-image: $linear2;
    z-index: 3;
    border-radius: 0 .5em .5em 0;
    img{
      width: 1.5em;
    }
  }
}

.menu{
  background-color: $secondary3;
  &__inner{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__login{
    position: absolute;
    right: 0;
    bottom: 100%;
    margin-bottom: 1.625em;
  }
  &__mail{
    display: flex;
    align-items: center;
    color: #FFFFFF;
    height: 2.75em;
    padding: 0 1em;
    img{
      width: 1.5em;
      display: block;
      flex-shrink: 0;
      margin-right: .75em;
    }
    &:hover{
      background: #5F6472;
      color: #fff;
    }
  }
  &__close{
    display: none;
  }
}

.nav{
  display: flex;
  &__item{
    flex-shrink: 0;
    position: relative;
    &._active{
      .nav__item-show{
        background: #5F6472;
        svg{
          transform: rotate(-180deg);
        }
      }
      .nav__item-hidden{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &-hidden{
      @media screen and (min-width: 993px){
        position: absolute;
        top: 100%;
        left: 0;
        min-width: 100%;
        transform: translateY(-1px);
        padding-top: 1px;
        opacity: 0;
        pointer-events: none;
        transition: all .3s;
      }
    }
  }
  &__link,
  &__item-show{
    display: flex;
    align-items: center;
    color: #fff;
    padding: 0 1em;
    height: 2.75em;
    font-size: 1em;
    transition: all .3s;
    img{

    }
    svg{
      margin-left: .75em;
      width: .75em;
      height: auto;
      transition: inherit;
    }
    &:hover{
      background: #5F6472;
      color: #fff;
      cursor: pointer;
    }
  }
  &__icon{
    width: 1.5em;
    display: block;
    flex-shrink: 0;
    margin-right: .75em;
    svg{
      width: 100%;
      height: auto;
      margin: 0;
    }
  }
  &__sublinks{
    border: $border;
    border-top: none;
    border-radius: 0 0 .625em .625em;
    background-color: #fff;
    overflow-x: hidden;
  }
  &__sublink{
    display: flex;
    align-items: center;
    padding: 0 1em;
    white-space: nowrap;
    height: 2.5em;
    font-size: 1em;
    font-weight: 500;
    background-color: $bg;
    &:not(:first-child){
      border-top: $border;
    }
    &:hover{
      background-color: transparent;
      color: $accent;
    }
  }
}

.menu-form{
  display: none;
}


@media screen and (max-width: 992px){
  .banner{
    display: none;
  }

  .banner2{
    display: block;
    padding: .3125em 1.75em;
    background-color: $bg;
    color: $linkColor;
  }

  .header{
    border-bottom: 2px solid $secondary3;
    &__logo{
      width: 10.25em;
    }
    &__buttons{
      display: flex;
    }
    &__mob-btn{
      font-size: 1em;
      width: 2.75em;
      height: 2.75em;
      display: block;
      img{
        width: 100%;
        display: block;
      }
    }
    &._open{
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }

  .header-top{
    &__inner{
      position: relative;
    }
  }

  .header-search{
    width: calc(100% - 2.75em);
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    &._active{
      opacity: 1;
      pointer-events: unset;
    }
  }

  .menu{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: #fff;
    padding: 4.75em 0 5em;
    overflow-y: auto;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    &__close{
      display: flex;
      font-size: 1em;
      position: absolute;
      top: 1.25em;
      right: 1.25em;
      img{
        width: 1.5em;
      }
    }
    &__inner{
      display: block;
    }
    &__login{
      position: static;
      width: 100%;
      margin: 2.5em 0 0;
    }
    &__mail{
      display: none;
    }
  }

  .nav{
    display: block;
    border-top: 1px solid #BFBFBF;
    &__item{
      &._active{
        .nav__item-show{
          color: $black;
        }
        .nav__icon{
          svg{
            transform: unset;
          }
        }
      }
      &-hidden{
        display: none;
      }
    }
    &__item-show,
    &__link{
      font-weight: 500;
      color: $black;
      border-bottom: 1px solid #BFBFBF;
      background-color: transparent !important;
      svg{
        path{
          stroke: $black;
        }
        &.fill-svg{
          path{
            stroke: transparent;
            fill: $black;
          }
        }
      }
      &:hover{
        color: $black;
      }
    }
    &__sublinks{
      border-left: none;
      border-right: none;
      border-radius: 0;
    }
    &__sublink{
      padding-left: 3.25em;
    }
  }

  .menu-form{
    display: block;
    margin-top: 2.5em;
    &__title{
      text-align: center;
    }
    &__fields{
      @include grid100gap(1em);
      margin-top: 1.5em;
    }
    &__btn{
      width: 100%;
      margin-top: 1.25em;
    }
  }
}
