@import "../mixins";
.auto{
  padding: 5em 0 3.75em;
  overflow-x: hidden;
  &__title{
    text-align: center;
  }
}

.auto-list{
  margin-top: 2.5em;
  border-radius: .9375em;
  border: 1px solid $accent2;
  padding: 0 1.125em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  z-index: 2;
  &__flag{
    display: block;
    padding: 0 .5em;
    img{
      width: 100%;
    }
  }
}

.auto-letter{
  position: relative;
  &._active{
    .auto-letter__dropdown{
      opacity: 1;
      pointer-events: unset;
    }
    .auto-letter__toggle{
      background-color: $bg;
      color: $accent;
    }
  }
  &__toggle{
    @include center;
    padding: 0 .75em;
    height: 2.625em;
    color: $grey;
    &:hover{
      cursor: pointer;
      background-color: $bg;
      color: $accent;
    }
  }
  &__dropdown{
    position: absolute;
    padding-top: 1px;
    top: 100%;
    left: 0;
    transform: translateY(-1px);
    min-width: 9em;
    opacity: 0;
    pointer-events: none;
  }
  &__links{
    border-radius: 0 .625em .625em .625em;
    border: $border;
    overflow-x: hidden;
    background-color: #fff;
    z-index: 5;
  }
  &__link{
    background-color: $bg;
    padding: .5em .75em;
    height: 2.625em;
    display: flex;
    align-items: center;
    &:not(:last-child){
      border-bottom: $border;
    }
    &:hover{
      background-color: transparent;
      color: $accent;
    }
  }
}

.auto-dealers{
  margin-top: 2.25em;
  overflow-x: hidden;
  .swiper{
    &-container{

    }
    &-slide{
      width: 11.625em;
      padding-right: 3.5em;
    }
  }
}

.auto-dealer{
  &__img{
    display: block;
    padding-bottom: 88%;
    position: relative;
    img{
      position: absolute;
      @include absolute;
      max-width: 100%;
      max-height: 100%;
    }
  }
}

.auto-brands{
  margin-top: 2.25em;
  &__grid{
    column-count: 4;
    column-gap: .75em;
  }
  &__nav{
    display: flex;
    margin-top: 1.25em;
  }
}

.auto-brand{
  display: flex;
  align-items: center;
  margin-bottom: .75em;
  &._disabled{
    color: $lightGrey;
    .grey-text2{
      color: inherit;
    }
  }
  &__title{
    font-weight: 500;
    margin-right: .5em;
  }
}

.popular{
  padding: 7.5em 0 8.75em;
  background-color: $bg;
  &__title{
    text-align: center;
  }
  &__groups{
    margin-top: 3.75em;
  }
  &__grid{
    @include grid3;
    margin-top: 1.5625em;
  }
  &__nav{
    display: flex;
    justify-content: flex-end;
    margin-top: 2.25em;
  }
  &__btn{
    padding: 0 1.75em;
  }
}

.news-card{
  background: #FFFFFF;
  border-radius: .625em;
  display: flex;
  flex-direction: column;
  &__img{
    width: 100%;
    position: relative;
    padding-bottom: 57.1%;
    display: block;
    img{
      border-radius: .625em .625em 0 0;
      @include imgCover;
    }
  }
  &__content{
    width: 100%;
    padding: .75em 1.25em 1.25em;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    .display4{
      margin-top: .185em;
    }
  }
  &__action{
    margin-top: auto;
    padding-top: 1.75em;
    display: flex;
    align-items: center;
  }
  &__btn{
    font-size: 1em;
    display: flex;
    align-items: center;
    color: $lightGrey;
    margin-right: 1.75em;
    img{
      width: 1.5em;
      display: block;
      flex-shrink: 0;
      margin-right: .5em;
    }
    span{
      font-size: .875em;
    }
  }
}


@media screen and (max-width: 992px){
  .auto{
    padding-top: 2.75em;
    overflow: unset;
  }

  .auto-list{
    margin-top: 1.75em;
    flex-wrap: wrap;
    justify-content: center;
    padding: 0 .15625em;
    position: relative;
  }

  .auto-letter{
    margin: 0 .15625em;
    position: static;
    &__toggle{
      padding: 0 .5em;
    }
    &__dropdown{
      min-width: unset;
      width: 100%;
      height: 100vh;
      background-color: rgba($black,.6);
      position: fixed;
      @include absolute;
      padding: 20px;
    }
    &__links{
      border-radius: .625em;
      width: calc(100% - 1em);
      position: absolute;
      @include absolute;
    }
  }

  .auto-dealers{
    margin: 1em 0 0 -1.25em;
    width: calc(100% + 2.5em);
    padding: 0 1.25em;
    .swiper{
      &-slide{
        width: 9.875em;
        padding-right: 1.75em;
      }
    }
  }

  .auto-brands{
    margin-top: 1em;
    &__grid{
      column-count: 2;
    }
    &__nav{
      margin-top: 1.75em;
    }
  }

  .auto-brand{
    .display4{
      font-size: 1.25em;
    }
  }

  .popular{
    padding: 5em 0 7.8125em;
    &__groups{
      margin-top: 2.5em;
    }
    &__grid{
      margin-top: 1.25em;
      .news-card{
        &:nth-child(2)~.news-card{
          display: none;
        }
      }
    }
    &__btn{
      width: 100%;
    }
  }
}
