.display1{
  font-size: 2.375em;
  line-height: 130%;
}

.display2{
  font-size: 1.875em;
  line-height: 130%;
}

.display3{
  font-size: 1.375em;
  line-height: 130%;
}

.display4{
  font-size: 1.375em;
  line-height: 140%;
}

.text18{
  font-size: 1.125em;
  line-height: 145%;
}

.text16{
  font-size: 1em;
  line-height: 125%;
}

.text14{
  font-size: .875em;
  line-height: 140%;
}

.bold-text{
  font-weight: 700;
}

.semi-bold-text{
  font-weight: 600;
}

.medium-weight-text{
  font-weight: 500;
}

.light-weight-text{
  font-weight: 300;
}

.accent2-text{
  color: $accent2;
}

.grey-text{
  color: $grey;
}

.grey-text2{
  color: $grey2;
}

.body-text{
  p{
    line-height: inherit;
    &:not(:last-child){
      margin-bottom: 10px;
    }
  }
}



@media screen and (max-width: 992px){
  .display1{
    font-size: 2em;
  }

  .display2{
    font-size: 1.625em;
  }

  .display3{
    font-size: 1.25em;
  }
}
