@import "../vars";
@import "../mixins";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    font-size: 1em;
    &:focus {
        box-shadow: none;
    }
    svg,
    img{
        display: block;
        width: 1.5em;
        height: auto;
        &:first-child{
            margin-right: .625em;
        }
        &:nth-child(2){
            margin-left: .625em;
        }
    }
}

.m-btn48{
    height: 3em;
    border-radius: .5em;
}

.m-btn50{
    height: 3.125em;
    border-radius: .3125em;
    span{
        font-size: 1.125em;
    }
}

.m-btn-black{
    padding: 0 1.5em;
    background-color: $secondary3;
    color: #FFFFFF;
    &:hover{
        background-color: #585A5F;
        color: #FFFFFF;
    }
}

.linear-btn{
    background-image: $linear2;
    position: relative;
    font-weight: 600;
    &:before{
        content: '';
        position: absolute;
        @include absolute;
        width: calc(100% - 4px);
        height: calc(100% - 4px);
        background-color: #fff;
        border-radius: inherit;
    }
    span{
        position: relative;
        z-index: 2;
        background-image: $linear2;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;
    }
    &:hover{
        background-image: $linear;
        span{
            background-image: $linear;
        }
    }
}


.accent-link{
    font-size: 1.25em;
    line-height: 130%;
    text-decoration: underline;
    font-weight: 500;
    color: $accent;
}
